import './App.css';
import React, { useState, useCallback, useEffect } from "react";
import { ethers } from "ethers";
import Frost from "./artifacts/contracts/Frost.sol/Frost.json";
import Serum from "./artifacts/contracts/Serum.sol/Serum.json";
import MutantYetis from "./artifacts/contracts/MutantYetis.sol/MutantYetis.json";

import { connectWallet, getCurrentWalletConnected, getERC20Balance, 
    approveERC20Token, getERC20Allowance, approveERC721Token, isERC721ApprovedForAll, 
    createMutantYetis
} from "./util/utils";

const constants = require("./constants")["MAIN"];

function App() {
  // Constants...
  const frostAddress = constants.frostAddress;
  const serumAddress = constants.serumAddress;
  const mutantYetisAddress = constants.mutantYetisAddress;
  const correctChainId = constants.correctChainId;
  const costFrost = constants.costFrost;

  // State...
  const [isLoading, setIsLoading] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const [isOnCorrectChain, setIsOnCorrectChain] = useState(false);
  const [frostBalance, setFrostBalance] = useState("0");
  const [serumBalance, setSerumBalance] = useState("0");
  const [frostAllowance, setFrostAllowance] = useState("0");
  const [isSerumApproved, setIsSerumApproved] = useState(false);
  const [isWalletConnected, setIsWalletConnected] = useState(false);
  const [forceDisconnectWallet, setForceDisconnectWallet] = useState(false);        // TODO:
  const [walletAddress, setWalletAddress] = useState("");
  const [numMutants, setNumMutants] = useState(0);

  // Set document title...
  useEffect(() => { document.title = "Mutant Yetis"; })

  // Listen to wallet/network changes...
  useEffect(() => { addWalletListener(); addNetworkListener(); }, []);

  // Loads all necessary data associated with the currently connected wallet...
  const loadWalletData = useCallback(async (address) => {
    setIsLoading(true);

    // Get wallet's balance...
    if (!!address) {
        const fBalance = await getERC20Balance(frostAddress, address);
        setFrostBalance(fBalance ? fBalance.toString() : "0");
        
        const sBalance = await getERC20Balance(serumAddress, address);
        setSerumBalance(sBalance ? sBalance.toString() : "0");

        const fAllowance = await getERC20Allowance(frostAddress, walletAddress, mutantYetisAddress);
        setFrostAllowance(fAllowance ? fAllowance.toString() : "0");

        const isApproved = await isERC721ApprovedForAll(serumAddress, walletAddress, mutantYetisAddress);
        setIsSerumApproved(isApproved);
    } else {
        setFrostBalance("0");
        setSerumBalance("0");
        setFrostAllowance("0");
        setIsSerumApproved(false);
    }

    setIsLoading(false);

  }, [walletAddress, setIsLoading, setFrostBalance, setSerumBalance, setFrostAllowance, setIsSerumApproved]);

  // Try to connect wallet on load...
  useEffect(() => {
    // Async because useEffect must be synchronous...
    async function checkWallet() {
        // Get connected wallet...
        const { address } = await getCurrentWalletConnected();
        setWalletAddress(address);
        setIsWalletConnected(!!address);

        if (!!address) { loadWalletData(address); }
    }

    // Check correct chain...
    setIsOnCorrectChain((window.ethereum && window.ethereum.chainId === correctChainId));

    // Check for wallet on load...
    checkWallet();
  }, [loadWalletData, setWalletAddress, setIsWalletConnected, setIsOnCorrectChain]);

  // "Connect Wallet" button...
  const onConnectWalletClick = useCallback(async () => {
    // Connect wallet...
    const { address } = await connectWallet();
    setWalletAddress(address);
    setIsWalletConnected(!!address);
    setForceDisconnectWallet(false);

    if (!!address) { loadWalletData(address); }
  }, [loadWalletData, setWalletAddress, setIsWalletConnected, setForceDisconnectWallet]);

  // "Disconnect Wallet" button...
  const onDisconnectWalletClick = useCallback(()=> {
    setWalletAddress("");
    setIsWalletConnected(false);
    setForceDisconnectWallet(true);

    setNumMutants(0);
    loadWalletData("");
  }, [setWalletAddress, setIsWalletConnected, setForceDisconnectWallet, setNumMutants, loadWalletData]);

  // "Approve Frost" button...
  const onApproveFrostClick = useCallback(async () => {
    setErrorMessage("");

    const amount = ethers.utils.parseUnits(costFrost, 18).mul(numMutants);
    const { success } = await approveERC20Token(Frost.abi, frostAddress, mutantYetisAddress, amount);
    if (!success) {  setErrorMessage("There was an error approving $Frost"); }
  }, [numMutants, setErrorMessage]);

  // "Approve Serum" button...
  const onApproveSerumClick = useCallback(async () => {
    setErrorMessage("");

    const { success } = await approveERC721Token(Serum.abi, serumAddress, mutantYetisAddress);
    if (!success) { setErrorMessage("There was an error approving Serum"); }
  }, [setErrorMessage]);

  // "Create Mutants" button...
  const onCreateMutantsClick = useCallback(async () => {
    setErrorMessage("");

    const { success } = await createMutantYetis(MutantYetis.abi, mutantYetisAddress, numMutants);
    if (!success) { setErrorMessage("There was an error creating the mutant(s)"); }
  }, [numMutants, setErrorMessage]);

  // Calculate the max number of MutantYetis user can create...
  const getMaxNumMutants = () => {
    const numBySerum = parseInt(serumBalance);
    const numByFrost = (ethers.utils.parseUnits(frostBalance, 0))
        .div(ethers.utils.parseUnits(costFrost, 18));
    
    // TODO: Include the max that are actually still available...
    return Math.min(numBySerum, numByFrost);
  };

  // +/- buttons...
  const onDecrementNumMutantsClick = useCallback(() => { if (numMutants > 0) { setNumMutants(numMutants - 1); } }, [numMutants, setNumMutants]);
  const onIncrementNumMutantsClick = useCallback(() => { 
      const maxMutantsCanCreate = getMaxNumMutants();
      if (numMutants < maxMutantsCanCreate) { setNumMutants(numMutants + 1); } 
  }, [numMutants, setNumMutants, getMaxNumMutants]);

  // Occurs when a wallet is changed...
  function addWalletListener() {
    if (!window.ethereum) { return; }
    window.ethereum.on("accountsChanged", (accounts) => {
        setWalletAddress(accounts.length > 0 ? accounts[0] : "");
        setIsWalletConnected(accounts.length > 0);
    });
  }

  // Occurs when a chain/network is changed...
  function addNetworkListener() {
    if (!window.ethereum) { return; }
    window.ethereum.on("chainChanged", (accounts) => {
        setIsOnCorrectChain((window.ethereum && window.ethereum.chainId === correctChainId));
        loadWalletData(walletAddress);
    });
  }

  const isCreateMutantsDisabled = (numMutants <= 0) || (!isWalletConnected) || (!isSerumApproved);
  const frostAllowanceNeeded = ethers.utils.parseUnits(costFrost, 18).mul(numMutants);
  const currentFrostAllowance = ethers.utils.parseUnits(frostAllowance, 0); // frostAllowance already has decimals incorporated
  const hasEnoughFrostAllowance = (frostAllowance) && currentFrostAllowance.gte(frostAllowanceNeeded);

  return (
    <div>
        {/* Navbar */}
        <div className="bg-black w-full md:h-14 sm:h-28 text-white">
            <div className="grid md:grid-cols-2 sm:grid-cols-1">
                <div>
                    <div className="h-14 gap-4 text-xl px-2 py-2 flex">
                        <a className="text-2xl col-span-2 self-center" href="http://cyberyetis.app">Cyber Yetis</a>
                        <a className="self-center hover:text-red-400" href="http://cyberyetis.info">Info</a>
                        <a className="self-center hover:text-red-400" href="http://cyberyetis.app">Staking</a>
                        <a className="self-center hover:text-red-400" href="http://serum.cyberyetis.app">Serum</a>
                    </div>
                </div>
                
                <div>
                    <div className="h-14 gap-4 px-2 py-2 align-center flex justify-end">
                        <a href="https://twitter.com/Cyber_Yetis" target="_blank" rel="noreferrer">
                            <img src={require("./assets/img/twitter.png")} alt="" className="inline max-h-8 px-1"></img>
                        </a>
                        <a href="https://discord.com/invite/7XqzdyPWH2" target="_blank" rel="noreferrer">
                            <img src={require("./assets/img/discord.png")} alt="" className="inline max-h-8 px-1"></img>
                        </a>

                        <a href="https://opensea.io/collection/cyberyetis" target="_blank" rel="noreferrer">
                            <img src={require("./assets/img/opensea.png")} alt="" className="inline max-h-8 px-1"></img>
                        </a>

                        <a href="https://polygonscan.com/address/0x4740Ff7a9C35c2D100D2cB7d489b166717D9C5A4" target="_blank" rel="noreferrer">
                            <img src={require("./assets/img/polygon.png")} alt="" className="inline max-h-8 px-1"></img>
                        </a>
                    </div>
                </div>
            </div>
        </div>

    <div id="root" className="self-start" style={{ padding: "10px" }}>
        <main className="">
            <div style={{ paddingTop: "20px" }}></div>
        
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                    
                    {/* Header/Description */}
                    <CyberYetiHeader>Mutant Yetis</CyberYetiHeader>
                    <MutantYetisDescription costFrost={costFrost} />
                    <Spacer />

                    {/* User's account information */}
                    <AccountBlock isLoading={isLoading} isOnCorrectChain={isOnCorrectChain}
                        isWalletConnected={isWalletConnected && !forceDisconnectWallet}
                        walletAddress={walletAddress}
                        frostBalance={frostBalance}
                        serumBalance={serumBalance}
                        frostAllowance={frostAllowance}
                        hasEnoughFrostAllowance={hasEnoughFrostAllowance}
                        isSerumApproved={isSerumApproved}
                        onDisconnectWalletClick={onDisconnectWalletClick}
                        onConnectWalletClick={onConnectWalletClick}
                        onApproveFrostClick={onApproveFrostClick}
                        onApproveSerumClick={onApproveSerumClick}
                    />
                </div>

                <div>
                    <div>
                        <img src={require("./assets/img/picture_mutants.png")} alt="" className="mx-auto max-h-96"></img>
                    </div>
                    <Spacer />

                    {/* Display an error if there is one */}
                    {!!errorMessage ? <ErrorBox>{errorMessage}</ErrorBox> : null}

                    {!isLoading && isWalletConnected && !forceDisconnectWallet ? <>

                    {/* Create Mutants Section */}
                    <CyberYetiBlackBox>
                        <div className="flex justify-center">
                            <div className="text-5xl">
                                {numMutants}
                                <button className="text-5xl" onClick={onDecrementNumMutantsClick}>-</button>
                                <button className="text-5xl" onClick={onIncrementNumMutantsClick}>+</button>
                            </div>
                            <div style={{ paddingLeft: "10px", paddingRight: "10px" }} />
                            <CyberYetiButton disabled={isCreateMutantsDisabled} onClick={onCreateMutantsClick}>Create Mutants</CyberYetiButton>
                        </div>
                    </CyberYetiBlackBox></> : null}
                    
                </div>            
            </div>
        </main>
    </div>
    </div>
  );
}

function Spacer() { return <div style={{ paddingTop: "10px" }}></div>; }

function CyberYetiHeader(props) {
    return <h1 className="text-center text-white text-6xl">{props.children}</h1>
}

function CyberYetiButton(props) {
    return <button {...props} className="rounded rounded-md p-2 text-xl text-black bg-white disabled:opacity-50" onClick={props.onClick}>{props.children}</button>
}

// Black box with white text. Can place anything you want inside
function CyberYetiBlackBox(props) {
    return <div {...props} className="rounded-lg bg-black p-10 text-white">{props.children}</div>;
}

// Red box with black text. Can place anything you want inside
function ErrorBox(props) {
    return <div className="flex justify-center bg-red-400 text-black">{props.children}</div>
}

// Description of Mutant Yetis
function MutantYetisDescription(props) {
    return (
        <CyberYetiBlackBox>
            <p className="text-white text-lg text-center">
                The yeti world is in shock! 
                Due to the serum developed by Professor Dr. Bernd Adler Yetis all over the world are showing 
                symptoms of changed personality and looks! 
                Yetis are starting fights, destroying things and even hurting other Yetis ... the public calls
                them MUTANTS.
            </p>
            <p style={{ color: "yellow" }} className="text-center">
                {`To create 1 Mutant, burn ${props.costFrost} $Frost and 1 Serum`}
            </p>
        </CyberYetiBlackBox>
    );
}

// Formatted account information
function AccountInformation(props) {
    return (
        <>
            <div className="text-ellipsis overflow-hidden">
                Account:<br />{props.walletAddress}
            </div>

            <div className="grid grid-cols-2 gap-2 pt-2">
                <div className="text-right">$Frost:</div>
                <div>{ethers.utils.formatUnits(props.frostBalance, 18).toString()}</div>

                <div className="text-right">Serum:</div>
                <div>{props.serumBalance}</div>

                <div className="text-right">Frost Allowance:</div>
                <div>{ethers.utils.formatUnits(props.frostAllowance, 18).toString()}</div>

                <div className="text-right">Serum Approved:</div>
                <div>{props.isSerumApproved.toString()}</div>
            </div>
        </>
    );
}

// Block that can show...
//   1) Loading message
//   2) Wrong network message
//   3) Connect Wallet button
//   4) Account information for connected wallet
function AccountBlock(props) {
    const { 
        isLoading, isOnCorrectChain, isWalletConnected,
        walletAddress, frostBalance, serumBalance, frostAllowance, isSerumApproved
    } = props;

    // If we are loading, return a loading message...
    if (isLoading) { 
        return <CyberYetiBlackBox><div className="flex justify-center">Loading...</div></CyberYetiBlackBox> ;
    }

    // If not on the correct chain/network, return an error message...
    if (!isOnCorrectChain) {
        return (
            <CyberYetiBlackBox>
                <ErrorBox>Please connect to the correct network.</ErrorBox>
                <div className="flex justify-center">
                    <CyberYetiButton onClick={props.onConnectWalletClick}>Connect Wallet</CyberYetiButton>
                </div>
            </CyberYetiBlackBox>
        );
    }

    // If wallet is not connected, ask them to connect their wallet...
    if (!isWalletConnected) {
        return (
            <CyberYetiBlackBox>
                <div className="flex justify-center">
                    <CyberYetiButton onClick={props.onConnectWalletClick}>Connect Wallet</CyberYetiButton>
                </div>
            </CyberYetiBlackBox>
        );
    }

    // Otherwise, return their account information...
    return (
        <CyberYetiBlackBox>
            <AccountInformation
                walletAddress={walletAddress}
                frostBalance={frostBalance}
                serumBalance={serumBalance}
                frostAllowance={frostAllowance}
                isSerumApproved={isSerumApproved}
            />
            <div className="flex justify-center pt-2">
                <CyberYetiButton onClick={props.onDisconnectWalletClick}>Disconnect Wallet</CyberYetiButton>
            </div>
            
            <div style={{ paddingTop: "20px" }} />
            <hr style={{ paddingTop: "20px" }} />

            <div className="text-center">
                Please approve the contract to spend your $Frost and Serum.
            </div>

            <div className="flex justify-center">
                <CyberYetiButton disabled={props.hasEnoughFrostAllowance} onClick={props.onApproveFrostClick}>Approve Frost</CyberYetiButton>
                <div style={{ paddingLeft: "5px" }} />
                <CyberYetiButton disabled={props.isSerumApproved} onClick={props.onApproveSerumClick}>Approve Serum</CyberYetiButton>
            </div>
        </CyberYetiBlackBox>
    )
}

// Block that shows "Approve Frost" and "Approve Serum"...
function ApprovalBlock(props) {
    return (
        <CyberYetiBlackBox>
            <div className="text-center">
                Please approve the contract to spend your $Frost and Serum.
            </div>

            <div className="flex justify-center">
                <CyberYetiButton disabled={props.hasEnoughFrostAllowance} onClick={props.onApproveFrostClick}>Approve Frost</CyberYetiButton>
                <div style={{ paddingLeft: "5px" }} />
                <CyberYetiButton disabled={props.isSerumApproved} onClick={props.onApproveSerumClick}>Approve Serum</CyberYetiButton>
            </div>
        </CyberYetiBlackBox>
    );
}

export default App;
