import { ethers } from "ethers";
import IERC20 from "./../artifacts/contracts/Frost.sol/IERC20.json";
import IERC721 from "./../artifacts/contracts/MutantYetis.sol/MutantYetis.json";

// Gets the balance of {addrWallet} of the {addrERC20} token (or null if we can't get it)...
export const getERC20Balance = async (addrERC20, addrWallet) => {
    if (typeof window.ethereum == "undefined") { return null; }

    const provider = new ethers.providers.Web3Provider(window.ethereum);
    const erc20 = new ethers.Contract(addrERC20, IERC20.abi, provider);
    try {
        const balance = await erc20.balanceOf(addrWallet);
        return balance;
    } catch(err) { 
        return null;
    }
};

// Connect to a wallet...
export const connectWallet = async () => {
    if (window.ethereum) {
      try {
        const addressArray = await window.ethereum.request({ method: "eth_requestAccounts", });
        const obj = { status: "", address: addressArray[0] };
        return obj;
      } catch (err) {
        return { status: err.message, address: "" };
      }
    } else {
      return {
        address: "",
        status: "You must install Metamask, a virtual Ethereum wallet, in your browser."
      };
    }
};

// Get currently connected wallet...
export const getCurrentWalletConnected = async () => {
    if (window.ethereum) {
      try {
        const addressArray = await window.ethereum.request({ method: "eth_accounts", });
        if (addressArray.length > 0) {
          return { address: addressArray[0], status: "Write a message in the text-field above.", };
        } else {
          return { address: "", status: "Connect to Metamask using the top right button.", };
        }
      } catch (err) {
        return { address: "", status: err.message, };
      }
    } else {
      return {
        address: "",
        status: "You must install Metamask, a virtual Ethereum wallet, in your browser."
      };
    }
};

// Approves {addrSpender} for {amount} on a given ERC20 token...
export const approveERC20Token = async (abiToken, addrToken, addrSpender, amount) => {
    if (typeof window.ethereum == "undefined") { return { success: false, status: "Ethereum not defined" }; }

    // Get contract...
    const signer = (new ethers.providers.Web3Provider(window.ethereum)).getSigner();
    const contract = new ethers.Contract(addrToken, abiToken, signer);

    // Send transaction...
    try {
        const txHash = await contract.approve(addrSpender, amount);
        return { success: true, status: "Check out your transaction: " + txHash }
    } catch (error) {
        return { success: false, status: "Something went wrong: " + error.message }
    }
};

// Gets the allowance of an ERC20 token...
export const getERC20Allowance = async (addrToken, owner, spender) => {
    if (typeof window.ethereum == "undefined") { return null; }

    const provider = new ethers.providers.Web3Provider(window.ethereum);
    const erc20 = new ethers.Contract(addrToken, IERC20.abi, provider);
    try {
        const allowance = await erc20.allowance(owner, spender);
        return allowance;
    } catch(err) { 
        return null;
    }
};

// Calls setApprovalForAll() for {addrApproval} for the given ERC721 token...
export const approveERC721Token = async (abiToken, addrToken, addrApproval) => {
    if (typeof window.ethereum == "undefined") { return { success: false, status: "Ethereum not defined" }; }

    // Get contract...
    const signer = (new ethers.providers.Web3Provider(window.ethereum)).getSigner();
    const contract = new ethers.Contract(addrToken, abiToken, signer);

    // Send transaction...
    try {
        const txHash = await contract.setApprovalForAll(addrApproval, true);
        return { success: true, status: "Check out your transaction: " + txHash }
    } catch (error) {
        return { success: false, status: "Something went wrong: " + error.message }
    }
};

// Calls setApprovalForAll() for {addrApproval} for the given ERC721 token...
export const isERC721ApprovedForAll = async (addrERC721, owner, operator) => {
    if (typeof window.ethereum == "undefined") { return false; }

    const provider = new ethers.providers.Web3Provider(window.ethereum);
    const erc721 = new ethers.Contract(addrERC721, IERC721.abi, provider);
    try {
        const isApproved = await erc721.isApprovedForAll(owner, operator);
        return isApproved;
    } catch(err) { 
        return false;
    }
};

// Calls MutantYetis.mint({amount})...
export const createMutantYetis = async(abiMutantYetis, addrMutantYetis, amount) => {
    if (typeof window.ethereum == "undefined") { return { success: false, status: "Ethereum not defined" }; }

    // Get contract...
    const signer = (new ethers.providers.Web3Provider(window.ethereum)).getSigner();
    const contract = new ethers.Contract(addrMutantYetis, abiMutantYetis, signer);

    // Send transaction...
    try {
        const txHash = await contract.mint(amount);
        return { success: true, status: "Check out your transaction: " + txHash }
    } catch (error) {
        return { success: false, status: "Something went wrong: " + error.message }
    }
};

